.booking-tab .rooms {
  .header {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
  }
  .room {
    padding: 0;
    margin: 5px 0;
    .ui-card {
      height: auto;
      align-items: center;
      justify-content: space-between;

      .ui-card__header {
        width: 140px !important;
        flex-grow: 0;
        flex-shrink: 0;
      }
      .ui-card__body {
        flex-direction: row;
        align-items: center;
        ul {
          flex-wrap: wrap;
          li {
            padding: 0 1rem;
          }
        }
      }
    }
    .schedule {
      margin-right: 5px;
    }
    .error {
      margin: 0;
      background-color: inherit;
    }
  }
}
