.meeting-tab .share-tab {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  .screen {
    position: relative;
    span {
      position: absolute;
      left: 0;
      bottom: 0;
      background-color: rgba(0,0,0,0.5);
      border-radius: 4px;
      padding: 2px 4px;
      margin: 2px;
      font-size: 0.8em;
    }
    img {
      max-height: 100%;
      max-width: 100%;
    }
    .placeholder {
      position: absolute;
      left: 0;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background-color: rgba(50, 50, 50, 1);
    }
  }
}
