.meeting-tab {
    color: rgb(225, 225, 225);
    &.compact { 
        background-color: rgb(32,31,31) !important;
        height: 100%;
        overflow: auto;
        > .narrow {
            padding: 0.5rem;
        }
    }
    .loader {
        width: 100vw;
        height: 100vh;
        color: #d7d7d7;
    }

    .clickshare-button {
        margin-left: -5px;
        margin-right: -5px;
    }

    .share-button {   
        color: #d7d7d7;
        font-size: 0.75rem;
        margin-top: 15px;
        text-decoration: underline;
        background: none!important;
        border: none;
        padding: 0!important;
        cursor: pointer;
    }
}
