.meeting-tab .rooms {
  
  color: rgb(37, 36, 35);
  .room {
    padding: 0;
    margin: 5px 0;
    &:hover, &:active {
      background: none;
    }
    .ui-card {
      .ui-card__header {
        width: 100%;
      }
    }
    .error {
      margin: 0;
      background-color: inherit;
    }
  }
}
