.booking-tab .upcoming-meetings {
  .rooms {
    position: relative;
    border-top: 1px solid #dddddd;
    border-radius: 0;

    .ui-card__body {
      width: 100%;
      .ui-list {
        flex-grow: 1;
      }
      .ui-icon {
        cursor: pointer;
        margin-left: 5px;
        position: absolute;
        right: 5px;
        top: 5px;
      }
    }
  }
  .meeting {
    padding: 0;
    margin: 5px 0;
    .ui-card {
      height: auto;
      align-items: center;
      justify-content: space-between;
        
      .location {
        display: flex;
        align-items: center;
        .ui-text {
          font-size: 0.9em;
          margin-right: 3px;
        }
      }
      .ui-card__body {
        flex-direction: row;
        align-items: center;
        ul {
          flex-wrap: wrap;
          li {
            padding: 0 1rem;
          }
        }
      }
    }
    .actions {
      flex-direction: row;
      button {
        margin-left: 5px;
      }
      .ui-icon {
        cursor: pointer;
      }
    }
  }
}
