
.clickshare-tab .rooms {
  
  position: absolute;
  left: 0;
  top: 0;
  /* margin: auto; */
  display: flex;
  width: 100%;
  height: 100%;
  background: rgba(70,70,70,0.9);
  align-items: center;
  justify-content: center;
  .loader {
    color: white;
  }
  ul {
    max-height: 80vh;
    overflow-x: hidden;
    overflow-y: auto;
    li:hover, li:active {
      background: none;
    }
  }
  .room {
    // width: 250px;
    padding: 0;
    margin: 5px 0;
    .ui-card {

      .ui-card__header {
        width: 100%;
      }
    }
    .error {
      margin: 0;
      background-color: inherit;
    }
  }
}
