.clickshare-button-img {
  width: 100px;
  height: 100px;
  border-radius:50%;
  background-color: #fff;
  -webkit-animation: glowing 1s ease-in-out infinite alternate;
  -moz-animation: glowing 1s ease-in-out infinite alternate;
  animation: glowing 1s ease-in-out infinite alternate;
}
@-webkit-keyframes glowing {
  from {
    box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #f0f, 0 0 40px #0ff, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073;
  }
  to {
    box-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6, 0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6, 0 0 80px #ff4da6;
  }
}
.clickshare-button {
  border: 1px solid #888888;
  border-radius: 10px;
  padding: 10px 10px;
  color: #eeeeee;
  background-color: #443535;  
  .loader {
    width: auto !important;
    height: auto !important;
  }
  .header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    .win {
      color: grey;
      border: 1px solid grey;
      border-radius: 9px;
      padding: 0 5px;
      > * {
        font-size: 0.8em;
        padding: 4px 6px;
        cursor: pointer;
      };
      .windows-button {
        border-right: 1px solid grey;
      }
    }
    .status-message {
      font-size: 0.8rem;
    }
  }
  .main {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .button-group {
      display: flex;
      .pause-button{
        width: 30px;
        height: 30px;
        cursor: pointer;
      }
      .circle-button {
        cursor: pointer;
        .inside-boarder {
          z-index: 1000;
          width: 25px;
          height: 25px;
          background-color: #443535;
          // background-color: #353535;
          border-radius: 50%;
        }
        background-color: #eeeeee;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        &.sharing {
          background-color: red;
        }
        &.inactive {
          background-color: #444444;
        }
      }
    }
  }
  .footer {
    display: flex;
    flex-direction: row-reverse;
    margin-top: 10px;
    .leave-button {
      cursor: pointer;
      font-size: 0.9rem;
      color: #999999;
    }
  }
}
