.clickshare-tab {
    background-color: rgb(33,33,33);

    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    .loader {
        width: 100vw;
        height: 100vh;
    }
    .toolbar {
        border-bottom: rgb(100,100,100) 1px solid;
        padding: 10px 0 10px 25px;
        align-items: center;
        font-size: 0.8rem;
        display: flex;
        color: rgb(225, 225, 225);

        .btn-disconnect {
            color: rgb(225, 225, 225);
            background-color: rgb(177, 56, 49);
            border: none;
        }
        .btn-reload {
            margin: 0 10px;
        }

        .room-info {
            position: absolute;
            left: 0;
            right: 0;
            text-align: center;
            width: 300px;
            margin-left: auto;
            margin-right: auto;
        }
        .next-meeting {
            align-self: center;
            margin-right: 15px;
            margin-left: auto;
            button {   
                color: rgb(225, 225, 225);
                margin-top: 5px;
                text-decoration: underline;
                background: none!important;
                border: none;
                padding: 0!important;
                text-decoration: underline;
                cursor: pointer;
                
                float: right;
            }
        }
    }

    .page-padding {
        padding: 1rem 3rem;
        
        overflow-y: auto;
        overflow-x: hidden;

    }
}
