.clickshare-button .screenshots {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 10px;
  // max-height: 300px;
  // overflow: auto;
  // overflow-x: hidden;
  margin-bottom: 10px;
  
  grid-column-gap: 3px;
  grid-row-gap: 3px;

  .header {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
    
    padding: 0 0 15px 0;
    h2 {
      margin-left: -20px;
      font-size: 0.8rem;
      font-weight: 500;
      color: rgb(225, 225, 225);
    }
  }

  .ui-card {
    margin: 0;
    width: auto;
    background-color: inherit;
    cursor: pointer;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100px;
    width: 110px;

    &.selected {
      border: 2px solid rgb(255,69,58);
      border-radius: 0;
    }
    .ui-card__preview {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 0;
      
      img {
        max-width: 100%;
        max-height: 100%;
        width: auto;
      }
      
      .controller {
        width: 100%;
        height: 100%;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(200,200,200, 0.5);
        color: rgb(255,69,58);
        font-size: 1.5rem;
        border: 2px solid rgb(255,69,58);
        > svg {
          cursor: pointer;
        }
      }
    }
    
    .ui-card__header {
      .ui-text {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        text-align: center;
        padding: 0.2rem 0;
        color: rgb(145,145,145);
        font-size: 0.7rem;
        font-weight: 500;
      }
    }
  }
  .screenshot {
    .error {
      margin: 0;
      background-color: inherit;
    }
  }
}
