#root > .ui-provider {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.app-loader {
  width: 100vw;
  height: 100vh;
}

.narrow {
  max-width: 900px;
  margin: 0 auto;
}

.page-padding {
  padding: 4rem;
}

.page > .narrow > img {
  margin: 0 auto;
  display: block;
  width: 200px;
}

.center {
  text-align: center;
}

.sections > * {
  margin: 4rem auto;
}

pre,
div.error {
  background-color: #e5e5e5;
  padding: 1rem;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  margin: 1rem 0;
  max-height: 200px;
  overflow-x: scroll;
  overflow-y: scroll;
}

pre.fixed,
div.error.fixed {
  height: 200px;
}

code {
  background-color: #e5e5e5;
  display: inline-block;
  padding: 0px 6px;
  border-radius: 3px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
}

.error {
  color: red;
}
