.clickshare-tab .screenshots {
  margin-bottom: 10px;
  .header {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
    
    padding: 0 0 15px 0;
    h2 {
      margin-left: -20px;
      font-size: 0.8rem;
      font-weight: 500;
      color: rgb(225, 225, 225);
    }
  }
  .group {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 40px;
    grid-row-gap: 30px;

    .ui-card {
      margin: 0;
      width: auto;
      background-color: inherit;
      
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .ui-card__preview {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 0;
        
        width: 240px;
        height: 140px;
        img {
          max-width: 100%;
          max-height: 100%;
          width: auto;
        }
        
        .controller {
          width: 100%;
          height: 100%;
          position: absolute;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: rgba(200,200,200, 0.5);
          color: rgb(255,69,58);
          font-size: 1.5rem;
          border: 2px solid rgb(255,69,58);
          > svg {
            cursor: pointer;
          }
        }
      }
      
      .ui-card__header {
        .ui-text {
          text-align: center;
          padding: 0.2rem 0;
          color: rgb(145,145,145);
          font-size: 0.7rem;
          font-weight: 500;
        }
      }
    }
  }
  .screenshot {
    .error {
      margin: 0;
      background-color: inherit;
    }
  }
}
